    left: 1rem;
    right: 1rem;
    transform: none;
    white-space: normal;
  }

  .ee-context-menu {
    min-width: 200px;
  }

  .ee-idle-code {
    font-size: 1.2rem;
  }

  .ee-blame-line {
    grid-template-columns: 55px 35px 65px 1fr;
    font-size: 0.6rem;
  }

  .ee-api-endpoint {
    flex-wrap: wrap;
    gap: 0.4rem;
  }

  .ee-api-desc {
    margin-left: 0;
    width: 100%;
  }

  .ee-selection-toast {
    left: 1rem;
    right: 1rem;
    transform: none;
    white-space: normal;
  }

  .ee-eof-toast {
    left: 1rem;
    right: 1rem;
    transform: none;
