  }

  .ee-rmrf-progress {
    width: 200px;
  }
}

/* ===== HERO SCANLINE & HUD ===== */

.hero-scanline {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: linear-gradient(90deg, transparent, rgba(99, 102, 241, 0.15), transparent);
  opacity: 0.6;
  animation: heroScanSweep 6s linear infinite;
  pointer-events: none;
  z-index: 3;
}

@keyframes heroScanSweep {
  0% { top: -2px; opacity: 0; }
  10% { opacity: 0.6; }
  90% { opacity: 0.6; }
  100% { top: 100%; opacity: 0; }
}

/* HUD Corner Brackets */
.hero-hud {
  position: absolute;
  width: 40px;
  height: 40px;
  z-index: 5;
  pointer-events: none;
  opacity: 0.15;
}

.hero-hud-tl {
