  border-left: 1px solid #818cf8;
}

.hero-hud-tr {
  top: 2rem;
  right: 2rem;
  border-top: 1px solid #818cf8;
  border-right: 1px solid #818cf8;
}

.hero-hud-bl {
  bottom: 2rem;
  left: 2rem;
