  margin-right: 0.4rem;
}

.hero-role-separator {
  color: #28c840;
  margin-right: 0.4rem;
  flex-shrink: 0;
  font-size: 1rem;
}

.hero-role-prefix {
  color: #6b6b80;
  margin-right: 0.5rem;
  flex-shrink: 0;
}

.hero-bio-wrapper {
  max-width: 650px;
  margin: 0 auto 2.5rem;
  font-size: 1.1rem;
  line-height: 1.8;
}

.hero-shimmer-btn {
  display: inline-flex;
  align-items: center;
  gap: 0.75rem;
  padding: 1rem 2.5rem;
  border-radius: 50px;
  font-weight: 600;
  font-size: 1.05rem;
  color: #d4d4e0;
  text-decoration: none;
  border: 1px solid rgba(255,255,255,0.1);
  background: linear-gradient(110deg, #0a0a0a 0%, #0a0a0a 40%, #1a1a3e 50%, #0a0a0a 60%, #0a0a0a 100%);
  background-size: 250% 100%;
  animation: shimmer 3s linear infinite;
  transition: all 0.3s ease;
}

.hero-shimmer-btn:hover {
  border-color: rgba(99, 102, 241, 0.5);
  box-shadow: 0 0 40px rgba(99, 102, 241, 0.25);
  color: white;
  transform: translateY(-2px);
}

.hero-shimmer-btn svg {
  width: 20px;
  height: 20px;
  transition: transform 0.3s ease;
}

.hero-shimmer-btn:hover svg {
  transform: translateY(2px);
}

/* CTA Row */
.hero-cta-row {
  display: flex;
  align-items: center;
  gap: 1rem;
  flex-wrap: wrap;
  justify-content: center;
}

/* Outline button */
.hero-outline-btn {
  display: inline-flex;
  align-items: center;
  gap: 0.75rem;
  padding: 1rem 2.5rem;
  border-radius: 50px;
  font-weight: 600;
  font-size: 1.05rem;
  color: #818cf8;
  text-decoration: none;
  border: 1px solid rgba(99, 102, 241, 0.25);
  background: transparent;
  transition: all 0.3s ease;
}

.hero-outline-btn:hover {
  border-color: rgba(99, 102, 241, 0.6);
  background: rgba(99, 102, 241, 0.05);
  box-shadow: 0 0 30px rgba(99, 102, 241, 0.15);
  color: #a5b4fc;
  transform: translateY(-2px);
}

.hero-outline-btn svg {
  width: 20px;
  height: 20px;
  transition: transform 0.3s ease;
}

.hero-outline-btn:hover svg {
  transform: scale(1.1);
}

.hero-secondary-links {
  display: flex;
  align-items: center;
  gap: 1.5rem;
  margin-top: 1.5rem;
}

.hero-secondary-links a,
.hero-secondary-links span {
  font-size: 0.85rem;
  color: #555;
  text-decoration: none;
  transition: color 0.3s ease;
}

.hero-secondary-links a:hover {
  color: #a5b4fc;
}

.hero-secondary-dot {
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background: #333;
}

.hero-scroll-new {
  position: absolute;
  bottom: 2rem;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
}

.hero-scroll-new span {
  font-size: 0.7rem;
  text-transform: uppercase;
  letter-spacing: 0.2em;
  color: #444;
}

@media (max-width: 768px) {
  .hero-name-new {
    font-size: 3rem;
  }
  .hero-role-line {
    font-size: 1.1rem;
  }
  .hero-avatar-wrapper {
    width: 130px;
    height: 130px;
  }
  .hero-logo {
    font-size: 2.6rem;
  }
  .hero-hud {
    width: 25px;
    height: 25px;
  }
  .hero-hud-tl, .hero-hud-bl { left: 1rem; }
  .hero-hud-tr, .hero-hud-br { right: 1rem; }
  .hero-shimmer-btn,
  .hero-outline-btn {
    padding: 0.85rem 1.8rem;
    font-size: 0.9rem;
  }
  .hero-status-badge {
    font-size: 0.65rem;
  }
}

@media (max-width: 480px) {
  .hero-avatar-wrapper {
    width: 110px;
    height: 110px;
    margin-bottom: 2rem;
  }
  .hero-logo {
    font-size: 2.2rem;
  }
  .hero-cta-row {
    flex-direction: column;
    width: 100%;
    gap: 0.75rem;
  }
  .hero-shimmer-btn,
  .hero-outline-btn {
    width: 100%;
    justify-content: center;
  }
  .hero-secondary-links {
    flex-direction: column;
    gap: 0.5rem;
  }
  .hero-secondary-dot {
    display: none;
  }
}

/* ═══════════════════════════════════════════════════
   Achievement Hub
   ═══════════════════════════════════════════════════ */

/* Floating button */
.ee-hub-btn {
  position: fixed;
  bottom: 1.25rem;
  left: 1.25rem;
  z-index: 9998;
  background: rgba(15, 15, 25, 0.92);
  border: 1px solid rgba(99, 102, 241, 0.35);
  border-radius: 8px;
  padding: 0.45rem 0.85rem;
  cursor: pointer;
  font-family: 'JetBrains Mono', monospace;
  font-size: 0.78rem;
  color: #818cf8;
  backdrop-filter: blur(12px);
  transition: border-color 0.3s, box-shadow 0.3s;
}
.ee-hub-btn:hover {
  border-color: rgba(99, 102, 241, 0.7);
  box-shadow: 0 0 16px rgba(99, 102, 241, 0.25);
}
.ee-hub-btn-text {
  pointer-events: none;
}

/* Pulse animation on new unlock */
.ee-hub-pulse {
  animation: eeHubPulse 1.5s ease-out;
}
@keyframes eeHubPulse {
  0%   { box-shadow: 0 0 0 0 rgba(99, 102, 241, 0.55); }
  70%  { box-shadow: 0 0 0 12px rgba(99, 102, 241, 0); }
  100% { box-shadow: 0 0 0 0 rgba(99, 102, 241, 0); }
}

/* Overlay */
.ee-hub-overlay {
  position: fixed;
  inset: 0;
  z-index: 9999;
  background: rgba(0, 0, 0, 0.72);
  backdrop-filter: blur(6px);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1rem;
}

/* Panel */
.ee-hub-panel {
  background: #0d0d14;
  border: 1px solid rgba(99, 102, 241, 0.3);
  border-radius: 12px;
  width: 100%;
  max-width: 640px;
  max-height: 85vh;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  box-shadow: 0 0 40px rgba(99, 102, 241, 0.12), 0 0 80px rgba(0, 0, 0, 0.5);
}

/* Title bar */
.ee-hub-titlebar {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.65rem 0.9rem;
  background: rgba(20, 20, 35, 0.95);
  border-bottom: 1px solid rgba(99, 102, 241, 0.15);
}
.ee-hub-titlebar-text {
  flex: 1;
  font-family: 'JetBrains Mono', monospace;
  font-size: 0.72rem;
  color: #6b7280;
  text-align: center;
}
.ee-hub-close {
  background: none;
  border: none;
  color: #6b7280;
  font-size: 1.2rem;
  cursor: pointer;
  padding: 0 0.25rem;
  line-height: 1;
  transition: color 0.2s;
}
.ee-hub-close:hover {
  color: #f87171;
}

/* Header */
.ee-hub-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.9rem 1.1rem 0.4rem;
}
.ee-hub-count {
  font-family: 'JetBrains Mono', monospace;
  font-size: 0.78rem;
  color: #9ca3af;
}

/* Progress bar */
.ee-hub-progress-wrap {
  display: flex;
  align-items: center;
  gap: 0.65rem;
  padding: 0 1.1rem 0.9rem;
}
.ee-hub-progress-bar {
  flex: 1;
  height: 8px;
  background: rgba(99, 102, 241, 0.1);
  border-radius: 4px;
  overflow: hidden;
}
.ee-hub-progress-fill {
  height: 100%;
  background: linear-gradient(90deg, #6366f1, #a855f7);
  border-radius: 4px;
}
.ee-hub-progress-pct {
  font-family: 'JetBrains Mono', monospace;
  font-size: 0.72rem;
  color: #818cf8;
  min-width: 2.5rem;
  text-align: right;
}

/* Achievement grid */
.ee-hub-grid {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  padding: 0 1.1rem 1.1rem;
  overflow-y: auto;
  flex: 1;
  scrollbar-width: thin;
  scrollbar-color: rgba(99, 102, 241, 0.25) transparent;
}
.ee-hub-grid::-webkit-scrollbar {
  width: 5px;
}
.ee-hub-grid::-webkit-scrollbar-track {
  background: transparent;
}
.ee-hub-grid::-webkit-scrollbar-thumb {
  background: rgba(99, 102, 241, 0.25);
  border-radius: 3px;
}

/* Achievement card */
.ee-hub-card {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 0.7rem 0.85rem;
  border-radius: 8px;
  border: 1px solid transparent;
  transition: border-color 0.3s, background 0.3s;
}

.ee-hub-card-unlocked {
  background: rgba(99, 102, 241, 0.06);
  border-color: rgba(99, 102, 241, 0.3);
}
.ee-hub-card-locked {
  background: rgba(30, 30, 40, 0.4);
  border-color: rgba(55, 55, 70, 0.3);
  opacity: 0.6;
}

.ee-hub-card-icon {
  font-size: 1.5rem;
  width: 2.2rem;
  text-align: center;
  flex-shrink: 0;
}

.ee-hub-card-info {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 0.15rem;
  min-width: 0;
}
.ee-hub-card-top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 0.5rem;
}
.ee-hub-card-name {
  font-family: 'JetBrains Mono', monospace;
  font-size: 0.82rem;
  color: #e5e7eb;
  font-weight: 600;
}
.ee-hub-card-locked .ee-hub-card-name {
  color: #4b5563;
  letter-spacing: 0.05em;
}

.ee-hub-rarity {
  font-family: 'JetBrains Mono', monospace;
  font-size: 0.65rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.08em;
  flex-shrink: 0;
}

.ee-hub-card-status {
  font-family: 'JetBrains Mono', monospace;
  font-size: 0.7rem;
  color: #34d399;
  font-weight: 500;
}
.ee-hub-card-hint {
  font-family: 'JetBrains Mono', monospace;
  font-size: 0.7rem;
  color: #6b7280;
  font-style: italic;
}

/* Responsive */
@media (max-width: 480px) {
  .ee-hub-btn {
    bottom: 0.75rem;
    left: 0.75rem;
    font-size: 0.7rem;
    padding: 0.35rem 0.65rem;
  }
  .ee-hub-panel {
    max-height: 90vh;
    border-radius: 10px;
  }
  .ee-hub-card-icon {
    font-size: 1.25rem;
    width: 1.8rem;
  }
  .ee-hub-card-name {
    font-size: 0.75rem;
  }
}
